  <md-toolbar
    id="toolbar"
    md-elevation="0"
    class="md-white md-absolute"
    :color-on-scroll="colorOnScroll"
  >
    <div class="md-toolbar-row md-collapse-lateral">
      <div class="md-toolbar-section-start">
        <h3 class="md-title">NOVOLHARUA</h3>
      </div>
      <div class="md-toolbar-section-end">
        <md-button
          class="md-just-icon md-simple md-toolbar-toggle"
          :class="{ toggled: toggledClass }"
          @click="toggleNavbarMobile()"
        >
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
        </md-button>

        <div class="md-collapse">
          <div class="md-collapse-wrapper">
            <mobile-menu nav-mobile-section-start="false">
              <!-- Here you can add your items from the section-start of your toolbar -->
            </mobile-menu>
            <md-list>

              <md-list-item
                href="javascript:void(0)"
                @click="scrollTopPage()"
              >
               <p>Inicio</p>
              </md-list-item>
              <md-list-item
                href="javascript:void(0)"
                @click="scrollToElement('mapa-site')"
              >
               <p>Mapa do Site</p>
              </md-list-item>
              <md-list-item
                href="javascript:void(0)"
                @click="scrollToElement('quem_somos')"
              >
               <p>Quem Somos</p>
              </md-list-item>
              <md-list-item
                href="javascript:void(0)"
                @click="scrollToElement('apoiadores')"
              >
               <p>Apoiadores</p>
              </md-list-item>
              <md-list-item
                href="javascript:void(0)"
                @click="scrollToElement('patrocinadores')"
              >
               <p>Patrocinadores</p>
              </md-list-item>
              <md-list-item
                href="javascript:void(0)"
                @click="scrollToElement('contatos')"
              >
               <p>Contato</p>
              </md-list-item>
              <li class="md-list-item">
                <a
                  href="javascript:void(0)"
                  class="md-list-item-router md-list-item-container md-button-clean dropdown"
                >
                  <div class="md-list-item-content">
                    <drop-down direction="down">
                      <md-button
                        slot="title"
                        class="md-button md-button-link md-white md-simple"
                        data-toggle="dropdown"
                      >
                        <p class="color-white">Informações Uteis</p>
                      </md-button>
                      <ul class="dropdown-menu dropdown-with-icons">
                        <li>
                          <a href="#/links">
                            <p>Links Uteis</p>
                          </a>
                        </li>
                        <li>
                          <a href="#/region">
                            <p>Regiões Atendidas</p>
                          </a>
                        </li>
                      </ul>
                    </drop-down>
                  </div>
                </a>
              </li>
               <md-list-item v-if="this.status != this.$store.state.logged_institution"
                href="javascript:void(0)"
              >
              <a href="#/events">
               <p>Proximos Eventos</p>
              </a>
              </md-list-item>
              <md-list-item v-if="this.$store.state.logged_institution!=null"
                href="javascript:void(0)"
              >
              <a @click="logout()">
                <p>Logout</p>
              </a>
              </md-list-item>
            </md-list>
          </div>
        </div>
      </div>
    </div>
  </md-toolbar>
</template>

<script>
let resizeTimeout;
function resizeThrottler(actualResizeHandler) {
  // ignore resize events as long as an actualResizeHandler execution is in the queue
  if (!resizeTimeout) {
    resizeTimeout = setTimeout(() => {
      resizeTimeout = null;
      actualResizeHandler();

      // The actualResizeHandler will execute at a rate of 15fps
    }, 66);
  }
}

import MobileMenu from "@/layout/MobileMenu";
export default {
  components: {
    MobileMenu
  },
  props: {
    icon: require("@/assets/img/team.png"),
    type: {
      type: String,
      default: "white",
      validator(value) {
        return [
          "white",
          "default",
          "primary",
          "danger",
          "success",
          "warning",
          "info"
        ].includes(value);
      }
    },
    colorOnScroll: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      toggledClass: false,
      infos:null
    };
  },
  computed: {
    showDownload() {
      const excludedRoutes = ["login", "landing", "profile"];
      return excludedRoutes.every(r => r !== this.$route.name);
    }
    
  },
  methods: {
    autenticaSessao(){
		  if(localStorage.logged_institution && localStorage.token){
		    this.$http.get("institution/validate_token?token="+localStorage.token).then(res => {
		      this.$http.get("institution/" + localStorage.logged_institution).then(res => {
		        this.$store.state.logged_institution = res.data;
		      }).catch(err => {
		      this.flashMessage.show({status: 'error', title: 'Error', message: this.$store.state.error.sessao})
		      this.$router.push('/');
		    });
		    }).catch(err => {
		      this.flashMessage.show({status: 'error', title: 'Error', message: this.$store.state.error.sessao})
		      this.$router.push('/');
		    });
		  }else{
		      this.flashMessage.show({status: 'error', title: 'Error', message: this.$store.state.error.sessao})
		    this.$router.push('/');
		  }
		},
    logout(){
      localStorage.logged_institution = null;
      localStorage.token = null;
      this.autenticaSessao();
    },
    bodyClick() {
      let bodyClick = document.getElementById("bodyClick");

      if (bodyClick === null) {
        let body = document.querySelector("body");
        let elem = document.createElement("div");
        elem.setAttribute("id", "bodyClick");
        body.appendChild(elem);

        let bodyClick = document.getElementById("bodyClick");
        bodyClick.addEventListener("click", this.toggleNavbarMobile);
      } else {
        bodyClick.remove();
      }
    },
    toggleNavbarMobile() {
      this.NavbarStore.showNavbar = !this.NavbarStore.showNavbar;
      this.toggledClass = !this.toggledClass;
      this.bodyClick();
    },
    scrollListener() {
      resizeThrottler(this.handleScroll);
    },
    scrollTopPage(){
      document.body.scrollTop = document.documentElement.scrollTop = 0
    },
    scrollToElement(element) {
      let element_id = document.getElementById(element);
      if (element_id) {
        element_id.scrollIntoView({ block: "start", behavior: "smooth" });
      }
    }
  },
  mounted() {
    document.addEventListener("scroll", this.scrollListener);
  },
  beforeDestroy() {
    document.removeEventListener("scroll", this.scrollListener);
  }
};
</script>
